import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_CUS, TZB_HTTP_CUS1, TZB_HTTP_CUS2, TZB_HTTP_CUS222 } from 'app/@uisftech/common/service/http.constant';
import { API } from './transform-query.http.constant';
@Injectable()
export class TransformQueryHttpService {
    constructor(public httpService: HttpService) {
    }
    //转化方案统计查询
    queryConvertStatisticsInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS2 + API.queryConvertStatisticsInfo, params)
    }
    //转化方案统计下载
    loadConvertStatisticsInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS2 + API.loadConvertStatisticsInfo, params)
    }
    //用户列表展示登记
    addUserListShow(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS222 + API.addUserListShow, params)
    }
    //用户列表查询
    selectUserListShow(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS222 + API.selectUserListShow, params)
    }
}